
<template>
  <div class="container">
    <div
      v-show="paymentStatus == 1"
      class="alert alert-success fs-1 fw-bold my-4"
    >
      پرداخت با موفقیت انجام شد
    </div>
    <div
      v-show="paymentStatus == 0"
      class="alert alert-danger fs-3 fw-bold my-4"
    >
      خطا در پرداخت در صورتیکه از حساب شما وجهی کسر شده است طی ۷۲ ساعت آینده به
      حسابتان برگردانده خواهد شد
    </div>

    <div v-if="!order">
      <span class="fas fa-spinner fa-spin fs-1 mt-3"></span>
    </div>

    <div v-else>
      <div class="align-items-center col d-flex row text-start">
        <div class="col">
          <img class="mb-2" style="width: 100px" src="../../assets/logo.svg" />
          کلاس آنلاین ستاوین
        </div>
        <div class="col">
          فاکتور شماره:
          {{ order.id }}
        </div>
        <div class="col">
          تاریخ صدور:
          {{ jmoment(order.created_at).format("jYYYY/jMM/jDD") }}
        </div>
      </div>
      <div class="row">
        <div class="col-12 text-start">
          فاکتور شده برای:
          {{ order.user.first_name }}
          {{ order.user.last_name }}
        </div>
      </div>
      <hr />
      <div class="row">
        <div class="col-12 fs-4">آیتم های فاکتور</div>
      </div>

      <div v-if="order">
        <table
          class="table table-responsive table-striped table-hover align-middle"
        >
          <thead>
            <tr>
              <th class="text-start">محصول</th>
              <th>کد</th>
              <th>تعداد</th>
              <th>قیمت واحد</th>
              <th>مشاهده</th>
            </tr>
          </thead>
          <tbody>
            <template v-for="item in order.orderDetail" :key="item.id">
              <template v-if="item.all_videos_buy">
                <tr>
                  <td class="text-start">
                    <img
                      style="width: 50px"
                      :src="
                        API_STORAGE_URL + item.product.main_image_thumb_path
                      "
                    />
                    {{ item.product.name }} - دوره کامل
                  </td>
                  <td>
                    {{ item.product.id }}
                  </td>
                  <td>
                    {{ item.number }}
                  </td>
                  <td>
                    {{ this.utils.monize(item.price) }}
                    تومان
                  </td>
                  <td>
                    <router-link
                      class="btn btn-success"
                      :to="{
                        name: 'Products',
                        params: {
                          productId: item.product.id,
                          buyed_before: 1,
                        },
                      }"
                    >
                      مشاهده
                    </router-link>
                  </td>
                </tr>
              </template>
              <template v-else>
                <template
                  v-for="itemDetails in item.productDetails"
                  :key="itemDetails.productDetailVideo.id"
                >
                  <tr>
                    <td>
                      <img
                        style="width: 50px"
                        :src="
                          API_STORAGE_URL + item.product.main_image_thumb_path
                        "
                      />
                      جلسه
                      {{ itemDetails.numName }}
                      -
                      {{ item.product.name }}
                    </td>
                    <td>
                      {{ itemDetails.productDetailVideo.id }}
                    </td>
                    <td>1</td>
                    <td>
                      {{ this.utils.monize(itemDetails.price) }}
                      تومان
                    </td>
                    <td>
                      <router-link
                        class="btn btn-success"
                        :to="{
                          name: 'VideoSession',
                          params: {
                            productId: item.product.id,
                            videoSessionId: itemDetails.id,
                          },
                        }"
                      >
                        مشاهده
                      </router-link>
                    </td>
                  </tr>
                </template>
              </template>
            </template>
          </tbody>
        </table>

        <div class="row">
          <div class="col">
            <p class="fs-4">پرداختی</p>

            <div>
              <table class="table table-responsive">
                <tbody>
                  <tr>
                    <td class="text-end">مجموع قیمت های محاسبه شده:</td>
                    <td dir="ltr" class="text-start">
                      {{ this.utils.monize(totalPrice) }}
                      تومان
                    </td>

                    <td class="text-end">مجموع تخفیف:</td>
                    <td dir="ltr" class="text-start">
                      {{ this.utils.monize(totalDiscount) }}
                      تومان
                    </td>

                    <td class="text-end">پرداخت شده:</td>
                    <td dir="ltr" class="text-start">
                      {{ this.utils.monize(order.amount) }}
                      تومان
                    </td>

                    <td class="text-end">نحوه پرداخت:</td>
                    <td dir="ltr" class="text-start">
                      {{ order.amount == 0 ? "رایگان" : "درگاه بانک ملت" }}
                    </td>

                    <template v-if="order.payments.length > 0">
                      <td class="text-end">کد رهگیری:</td>
                      <td dir="ltr" class="text-start">
                        {{ order.payments[0].sale_reference_id }}
                      </td>
                    </template>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="my-4">
      <router-link class="btn btn-success btl-lg" :to="{ path: '/' }">
        بازگشت به پرتال
      </router-link>
    </div>
  </div>
</template>
<script>
import ArefApi from "../../libs/api.js";
import utils from "../../libs/utils";
import jmoment from "moment-jalaali";

export default {
  name: "Purchase",
  created() {
    this.arefApi = new ArefApi(this);
    this.utils = new utils();
    this.jmoment = jmoment;
    setTimeout(() => {
      this.getOrder();
    }, 3000);
    this.API_STORAGE_URL = process.env.VUE_APP_API_STORAGE_URL;
  },
  data() {
    return {
      orderId: this.$route.params.orderId ? this.$route.params.orderId : null,
      paymentStatus: Number(this.$route.params.status),
      order: null,
      OrderIndex: 0,
    };
  },
  methods: {
    getOrder() {
      this.$store.commit("set", ["pathAfterLogin", this.$route.fullPath]);
      this.arefApi
        .getOrder(this.orderId)
        .then((resp) => {
          this.order = resp.data;
          if (resp.data.length == 0 || resp.data.orderDetail.length == 0) {
            this.order = null;
          }
          this.getCart();
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    getCart() {
      this.arefApi
        .getCart()
        .then((resp) => {
          this.$store.commit("set", [
            "cartCount",
            this.utils.getCartCount(resp.data),
          ]);
          this.$store.commit("set", ["cartAmount", resp.data.amount]);
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
  },
  computed: {
    totalDiscount: function () {
      if (!this.order || !this.order.orderDetail) {
        return 0;
      }
      return this.order.orderDetail.reduce(function (totalDiscount, item) {
        return (
          totalDiscount + (item.total_price - item.total_price_with_coupon)
        );
      }, 0);
    },
    totalPrice: function () {
      if (!this.order || !this.order.orderDetail) {
        return 0;
      }
      return this.order.orderDetail.reduce(function (totalPrice, item) {
        return totalPrice + item.total_price;
      }, 0);
    },
  },
};
</script>